import {
  lunarInfo, solarTermInfo, solarTerm,
} from './constMap';

// 根据时间戳处理数据格式
interface IObjectAny {
  [propName: string]: any;
}

// 2023-08-06 | 2023年9月 | 6
export const formatDate = (date: Date, format = 'yyyy-MM-dd hh:mm:ss TT'): string => {
  const o: IObjectAny = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时 24进制
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds(), // 毫秒
    'T+': date.getHours() < 12 ? 'AM' : 'PM',
    'H+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 小时 12进制
  };
  if (/(y+)/.test(format)) {
    format = format.replace(RegExp.$1, String(date.getFullYear()).substr(4 - RegExp.$1.length));
  }
  Object.keys(o).forEach(k => {
    if (new RegExp(`(${k})`).test(format)) {
      format = format.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? (o[k]) : `00${o[k]}`.substr(String(o[k]).length),
      );
    }
  });
  return format;
};

// 本年第32周，第218天
export const countdownFormat = (date: Date, format = '本年第weeks周，第days天'): string => {
  const firstDay = new Date();
  firstDay.setMonth(0);
  firstDay.setDate(1);
  const days = Math.ceil((date.getTime() - firstDay.getTime()) / (24 * 60 * 60 * 1000));
  const weeks = Math.ceil(days / 7);
  return format.replace('weeks', `${weeks}`).replace('days', `${days}`);
};

// 传入阳历年月日获得详细的公历、农历object信息
export const getLunarDate = (date: Date) => {
  // 当前年月日
  const y = date.getFullYear();
  const m = date.getMonth() + 1;
  const d = date.getDate();

  // 是否是今天
  const todayDate = new Date();
  const isToday = todayDate.getFullYear() === y
    && todayDate.getMonth() + 1 === m
    && todayDate.getDate() === d;

  // 计算农历的时间差 即 当前农历年度过的天数
  let lunarYearDaysOffset = (Date.UTC(y, m - 1, d) - Date.UTC(1900, 0, 31)) / (24 * 60 * 60 * 1000);
  // 当前农历是哪年
  let lunarYear = 0;
  // 当前农历年的天数
  let lunarYearDays = 0;

  // 计算当前是农历年份
  for (lunarYear = 1900; lunarYear < 2101 && lunarYearDaysOffset > 0; lunarYear++) {
    lunarYearDays = getLunarYearDays(lunarYear);
    lunarYearDaysOffset -= lunarYearDays;
  }

  if (lunarYearDaysOffset < 0) {
    lunarYearDaysOffset += lunarYearDays;
    lunarYear--;
  }

  // 获取当前农历闰月是哪个月
  const lunarLeapMonth = getLunarLeapMonth(lunarYear);
  // 当前农历月份是
  let lunarMonth = 0;
  // 当前农历日是
  let lunarDay = 0;
  // 当前农历本月已过天数 用于计算
  let lunarDayOffset = lunarYearDaysOffset;
  // 当前农历本月天数 用于计算
  let lunarDays = 0;
  let isLeap = false; // 是否已经闰月过了
  // 计算当前农历月份和日期 注意 闰二月是在二月之后
  for (lunarMonth = 1; lunarMonth < 13 && lunarDayOffset > 0; lunarMonth++) {
    // 校验闰月
    if (lunarLeapMonth > 0 && lunarMonth === lunarLeapMonth + 1 && !isLeap) {
      lunarDays = getLunarLeapMonthDays(lunarYear);
      isLeap = true;
      --lunarMonth;
    } else {
      lunarDays = getLunarUnLeapMonthDays(lunarYear, lunarMonth);
    }
    if (isLeap && lunarMonth === lunarLeapMonth + 1) { isLeap = false; }
    // 处理农历当前月已过天数 即 总天数相减
    lunarDayOffset -= lunarDays;
  }

  if (lunarDayOffset < 0) {
    lunarDayOffset += lunarDays;
    lunarMonth = lunarMonth - 1;
  }
  lunarDay = lunarDayOffset + 1;

  return {
    solar: {
      year: y,
      month: m,
      day: d,
    },
    lunar: {
      year: lunarYear,
      month: lunarMonth,
      day: lunarDay,
      yearDays: lunarYearDays,
      yearDaysOffset: lunarYearDaysOffset,
      leapMonth: lunarLeapMonth,
      isLeap,
    },
    isToday,
  };
};

// 返回农历y年一整年的总天数
const getLunarYearDays = (lYear: number): number => {
  let i, sumDay, info = lunarInfo[lYear - 1900];
  sumDay = 348;
  for (i = 0x8000; i > 0x8; i >>= 1) {
    sumDay += (info & i) ? 1 : 0;
  }
  // 夏月是有可能跨两个月的
  return sumDay + getLunarLeapMonthDays(lYear);
};

// 返回农历y年闰月是哪个月；若y年没有闰月 则返回0
const getLunarLeapMonth = (lYear: number) => {
  return (lunarInfo[lYear - 1900] & 0xf);
};

// 返回农历y年闰月的天数 若该年没有闰月则返回0 其他29、30
const getLunarLeapMonthDays = (lYear: number) => {
  if (getLunarLeapMonth(lYear)) {
    return (lunarInfo[lYear - 1900] & 0x10000) ? 30 : 29;
  } else {
    return 0;
  }
};

// 返回农历y年m月（非闰月）的总天数，计算m为闰月时的天数请使用leapDays方法
const getLunarUnLeapMonthDays = (lYear: number, lMonth: number) => {
  return ((lunarInfo[lYear - 1900] & (0x10000 >> lMonth)) ? 30 : 29);
};

// 返回阳历y年m月的总天数
const getSolarMonthDays = (sYear: number, sMonth: number) => {
  return new Date(sYear, sMonth, 0).getDate();
};

// 返回这个阳历对应的所有节气数据
export const getSolarTerms = (sYear: number) => {
  const table = solarTermInfo[sYear - 1900];
  const tableInfo = [
    parseInt('0x' + table.substring(0, 5), 16).toString(),
    parseInt('0x' + table.substring(5, 10), 16).toString(),
    parseInt('0x' + table.substring(10, 15), 16).toString(),
    parseInt('0x' + table.substring(15, 20), 16).toString(),
    parseInt('0x' + table.substring(20, 25), 16).toString(),
    parseInt('0x' + table.substring(25, 30), 16).toString()
  ];
  const termsDay: any[] = [];
  tableInfo.forEach(i => {
    termsDay.push(+i.substring(0, 1));
    termsDay.push(+i.substring(1, 3));
    termsDay.push(+i.substring(3, 4));
    termsDay.push(+i.substring(4, 6));
  });
  const terms: any = {};
  termsDay.forEach((n, i) => {
    terms[`${Math.ceil((i + 1) / 2)}-${n}`] = solarTerm[+i];
  });
  return terms;
};

