/**
 * 说明
 * 此数据为人为梳理后统计规划，减少表数据量
 * 将地球绕太阳公转划分24等分后确定的二十四个时间点即为二十四节气
 * 而阳历又是地球绕太阳公转一周来划分年份的
 * 小寒算起阳历月的月份和二十四节气的顺序是一一对应的
 * 阳历中每个月有两个节气，这两个节气上半月节气阳历日必定是1位数，下半个月的阳历日必定为2位数
 * 所以将1至12月每2个月的四个节气的阳历日数据拼接成一个长度为6的十进制数，将这个十进制数转换为16进制（去除开头的0x长度是5）
 * 将这6组数据拼接之后就形成了
 * 9778397bd097c36b0b6fc9274c91aa => 97783 97bd0 97c36 b0b6f c9274 c91aa
 * => ['620419', '621520', '621622', '723823', '823924', '823722']
 * => ['6', '20', '4', '19', '6', '21', '5', '20', '6', '21', '6', '22', '7', '23', '8', '23', '8', '23', '9', '24', '8', '23', '7', '22']
 * => 即 小寒 01-06 谷雨 04-20
 * solarTermInfo记录了1900-2100整整两百年的每一年的二十四节气的所在的日数据
 */
