import { getLunarDate, formatDate, getSolarTerms } from '@/module/utils/tools';
import {
  week2string, animals,
  astroDays, astroStr, astroEmojiStr,
  gan, zhi,
  year2lunar, day2lunar, month2lunar, num2lunar,
  solarFestival, solarOtherFestival,
  lunarFestival, lunarOtherFestival,
} from '@/module/utils/constMap';

// 格式化详情页面所需的所有数据
export const dateFormat = (date: Date) => {
  const { isToday, lunar, solar } = getLunarDate(date);
  const firstDay = new Date();
  firstDay.setMonth(0);
  firstDay.setDate(1);
  const days = Math.ceil((date.getTime() - firstDay.getTime()) / (24 * 60 * 60 * 1000));
  const weeks = Math.ceil(days / 7);
  return {
    isToday,
    solar,
    lunar,
    // 第几周
    weeks,
    // 第几天
    days,
    // 2023-08-06 | 2023年9月 | 6
    date: (format = 'yyyy-MM-dd hh:mm:ss TT'): string => formatDate(date, format),
    // 周日
    week: (format = '周W'): string => format.replace('W', week2string[date.getDay()]),
    // 生肖
    animal: () => animals[(lunar.year - 4) % 12],
    // 星座
    astro: (format = 'AS座 as') => {
      // 星座 魔羯座(12/22 - 1/19)、水瓶座(1/20 - 2/18)、双鱼座(2/19 - 3/20)、牡羊座(3/21 - 4/20)、金牛座(4/21 - 5/20)、双子座(5/21 - 6/21)、巨蟹座(6/22 - 7/22)、狮子座(7/23 - 8/22)、处女座(8/23 - 9/22)、天秤座(9/23 - 10/22)、天蝎座(10/23 - 11/21)、射手座(11/22 - 12/21)
      // 对应星座的日期长度 是固定的 所以 21,19,21,20,21,22,23,23,23,24,23,22 可相当于 102223444433 + 19
      const month = date.getMonth() + 1;
      const boundary = astroDays[month - 1];
      const index = month - (date.getDate() < boundary ? 1 : 0);
      return format.replace('AS', astroStr.substring(index * 2, index * 2 + 2))
        .replace('as', astroEmojiStr.substring(index * 2, index * 2 + 2));
    },
    // 天干
    tianGan: () => gan[((lunar.year - 3) % 10 || 10) - 1],
    // 地支
    diZhi: () => zhi[((lunar.year - 3) % 12 || 12) - 1],
    // 农历年-中文 2023 -> 二〇二三年
    toChinaYear: () => `${lunar.year}`.split('').map(i => year2lunar[+i]).join('') + '年',
    // 农历月-中文 闰二月 腊月
    toChinaMonth: () => (lunar.isLeap ? '闰' : '') + month2lunar[lunar.month - 1] + '月',
    // 农历日-中文
    toChinaDay: (lunarDay = lunar.day) => `0${lunarDay === 10 ? '00' : lunarDay}`.substr(-2).split('').map((i, index) => index === 0 ? day2lunar[+i] : num2lunar[+i]).join(''),
    // 二十四节气
    solarTerm: (solarYear = solar.year, solarMonth = solar.month, solarDay = solar.day) => {
      const solarTerms = getSolarTerms(solarYear);
      return solarTerms[`${+solarMonth}-${+solarDay}`] || null;
    },
    // 阳历节日
    solarFestival: (solarMonth = solar.month, solarDay = solar.day) => solarFestival[`${+solarMonth}-${+solarDay}`] || null,
    // 阴历节日
    lunarFestival: (lunarMonth = lunar.month, lunarDay = lunar.day) => lunarFestival[`${+lunarMonth}-${+lunarDay}`] || null,
    // 阳历其他节日
    solarOtherFestival: () => solarOtherFestival[`${+solar.month}-${+solar.day}`] || [],
    // 阴历其他节日
    lunarOtherFestival: () => lunarOtherFestival[`${+lunar.month}-${+lunar.day}`] || [],
    // 黄历
    // 日历表
    calendar: () => {
      const rows = 7;
      const cols = 6;
      const currentWeekDay = new Date(solar.year, solar.month - 1, 1).getDay();
      // +1 是为了 日开头 变为 一开头 整体错位一天
      const days = Array(rows * cols + 1).fill('').map((n, index) => {
        return getLunarDate(new Date(solar.year, solar.month - 1, 0 - currentWeekDay + 1 + index));
      });
      return days;
    },
  };
};
