import React from 'react';
import classnames from 'classnames';
import {
  // Segmented,
  Button, Space, ConfigProvider
} from 'antd';
import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/store';
import { dateFormat } from '../../utils/format';
import styles from './index.less';

const selector = (store: any) => ({
  appThemeColor: store.appThemeColor,
  currentDate: store.currentDate,
  updateCurrentDate: store.updateCurrentDate,
});

function Calendar() {
  const { appThemeColor, currentDate, updateCurrentDate } = useStore(selector, shallow);
  const date = dateFormat(currentDate);
  const calendar = date.calendar();

  return (
    <div className={styles.container} style={{ '--app-theme-color': appThemeColor } as any}>
      <ConfigProvider autoInsertSpaceInButton={false} theme={{ token: { colorPrimary: appThemeColor } }}>
        <div className={styles.header}>
          <div className={styles.title}>
            {date.date('yyyy年M月')}
          </div>
          <div>
            <Space>
              {/* <Segmented defaultValue="月" options={['日', '周', '月', '年']} /> */}
              <Space.Compact block>
                <Button
                  icon={<LeftOutlined />}
                  onClick={() => updateCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1, 1))}
                />
                <Button onClick={() => updateCurrentDate(new Date())}>今日</Button>
                <Button
                  icon={<RightOutlined />}
                  onClick={() => updateCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 1))}
                />
              </Space.Compact>
            </Space>
          </div>
        </div>
      </ConfigProvider>
      <div className={styles.calendar}>
        <ul className={styles.head}>
          {['日', '一', '二', '三', '四', '五', '六'].map((i, index) => (
            <li
              key={`head-${index}`}
              className={classnames(styles.item, [6, 7].includes(index % 7 || 7) && styles.weekend)}
            >
              {i}
            </li>
          ))}
        </ul>
        <ul className={styles.body}>
          {calendar.slice(0, 42).map((i, index) => {
            const festival =
              // 二十四节气
              date.solarTerm(i.solar.year, i.solar.month, i.solar.day) ||
              // 阴历节日
              date.lunarFestival(i.lunar.month, i.lunar.day) ||
              // 阳历历节日
              date.solarFestival(i.solar.month, i.solar.day) ||
              null;

            return (
              <li key={`body-${index}`} className={styles.item}>
                <div
                  className={classnames(
                    styles.inner,
                    i.isToday && styles.today,
                    i.solar.month !== date.solar.month && styles.other,
                    festival && styles.festival,
                    [6, 7].includes(index % 7 || 7) && styles.weekend,
                    (date.solar.year === i.solar.year && date.solar.month === i.solar.month && date.solar.day === i.solar.day) && styles.selected,
                  )}
                  onClick={() => updateCurrentDate(new Date(i.solar.year, i.solar.month - 1, i.solar.day))}
                >
                  <div className={styles.info}>
                    <b>{`00${i.solar.day}`.substr(-2)}</b>
                    <i>
                      {
                        festival || date.toChinaDay(i.lunar.day) // 阴历
                      }
                    </i>
                  </div>
                  <div className={styles.tags}>
                    {i.isToday && (<span className={styles.isToday}>今</span>)}
                    {/* <span className={styles.isRest}>休</span> */}
                  </div>
                  {/* <u v-if="day.isHoliday">{{ day.isRest ? '休' : '班' }}</u> */}
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}

export default Calendar;
